.hidden {
  display: none;
}

.message-block {
  line-height: 26px;
  position: relative;
  transition: 0.5s all;
  -webkit-transition: 0.5s all;
  -moz-transition: 0.5s all;
  -ms-transition: 0.5s all;
  -o-transition: 0.5s all;
}

.message-block-last {
  margin-bottom: 15px;
}

.right .message,
.left .message {
  position: relative;
  transition: 1s opacity, all 0.4s;
  -webkit-transition: 1s opacity, all 0.4s;
  -moz-transition: 1s opacity, all 0.4s;
  -ms-transition: 1s opacity, all 0.4s;
  -o-transition: 1s opacity, all 0.4s;
  opacity: 0;
}

@-webkit-keyframes bubbleleft {
  0% {
    opacity: 0;
    -webkit-transform: rotate(-45deg) scaleX(-1);
    transform: rotate(-45deg) scaleX(-1);
    left: -3.5px;
  }

  100% {
    opacity: 100%;
    -webkit-transform: rotate(0) scaleX(-1);
    transform: rotate(0) scaleX(-1);
  }
}

@keyframes bubbleleft {
  0% {
    opacity: 0;
    -webkit-transform: rotate(-45deg) scaleX(-1);
    transform: rotate(-45deg) scaleX(-1);
    left: -3.5px;
  }

  100% {
    opacity: 100%;
    -webkit-transform: rotate(0) scaleX(-1);
    transform: rotate(0) scaleX(-1);
  }
}

@-webkit-keyframes bubbleRight {
  0% {
    opacity: 0;
    -webkit-transform: rotate(45deg) scaleX(1);
    transform: rotate(45deg) scaleX(1);
    right: -4.5px;
  }

  100% {
    opacity: 100%;
    -webkit-transform: rotate(0) scaleX(1);
    transform: rotate(0) scaleX(1);
  }
}

@keyframes bubbleRight {
  0% {
    opacity: 0;
    -webkit-transform: rotate(45deg) scaleX(1);
    transform: rotate(45deg) scaleX(1);
    right: -4.5px;
  }

  100% {
    opacity: 100%;
    -webkit-transform: rotate(0) scaleX(1);
    transform: rotate(0) scaleX(1);
  }
}

.left>svg {
  position: absolute;
  left: -15.5px;
  bottom: -1px;
  -webkit-transform: rotate(-45deg) scaleX(-1);
  -ms-transform: rotate(-45deg) scaleX(-1);
  transform: rotate(-45deg) scaleX(-1);
  -webkit-animation: bubbleleft 0.6s;
  animation: bubbleleft 0.6s;
  -webkit-animation-fill-mode: forwards;
  animation-fill-mode: forwards;
  -webkit-animation-delay: 0.6s;
  animation-delay: 0.6s;
  fill: var(--left-svg-fill);
  opacity: 0;
}

.right>svg {
  position: absolute;
  right: -16.5px;
  bottom: -2px;
  -webkit-transform: rotate(45deg) scaleX(1);
  -ms-transform: rotate(45deg) scaleX(1);
  transform: rotate(45deg) scaleX(1);
  -webkit-animation: bubbleRight 0.6s;
  animation: bubbleRight 0.6s;
  -webkit-animation-fill-mode: forwards;
  animation-fill-mode: forwards;
  -webkit-animation-delay: 0.6s;
  animation-delay: 0.6s;
  fill: #0253e2;
  opacity: 0;
}

.message-block .show {
  opacity: 100%;
}

.right[data-emergence=hidden] .message {
  left: 500px;
}

.right[data-emergence=visible] .message {
  left: 0;
  opacity: 100%;
}

.left[data-emergence=hidden] .message {
  left: -500px;
}

.left[data-emergence=visible] .message {
  left: 0px;
  opacity: 100%;
}

.message-block {
  display: block;
  width: 100%;
  max-width: 675px;
  /* max-width: 860px; */
  margin-bottom: 10px;
}

.message {
  border-radius: 30px;
  display: inline-block;
  padding: 15px 25px;
  line-height: 34px;
  font-size: 24px;
}

.right .message {
  background-color: #0253e2;
  color: white;
  float: right;
}

.left .message {
  color: var(--left-message-color);
  background-color: var(--left-message-bg-color);
}


.button-group {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  margin-top: 75px;
  margin-bottom: 75px;
}


.btn {
  margin: 0 10px;
  background-color: transparent;
  padding: 10px 20px;
  border: var(--btn-border);
  -webkit-transition: 0.3s all;
  -o-transition: 0.3s all;
  transition: 0.3s all;
  font-size: 20px;
  color: var(--btn-text-color);
  border-radius: 50px;
}

.btn:hover {
  background-color: var(--btn-bg-color-hover);
  cursor: pointer;
}

.btn:focus {
  border-color: var(--border-color);
  outline: none;
}






.profile {
  z-index: 2;
  -webkit-transform: scale(0.5);
  -ms-transform: scale(0.5);
  transform: scale(0.5);
  opacity: 0%;
  margin-top: 25%;
  position: relative;
  display: block;
  margin-left: auto;
  margin-right: auto;
  height: 200px;
  width: 200px;
  -webkit-transition: all 0.4s ease;
  -o-transition: all 0.4s ease;
  transition: all 0.4s ease;
}

.profile.animate {
  -webkit-transform: scale(1);
  -ms-transform: scale(1);
  transform: scale(1);
  opacity: 100%;
}

.profile.animate.smaller {
  margin-top: 23px;
  position: relative;
  display: block;
  left: 2%;
  width: 90px;
  height: 90px;
}

