  -o-transition: all 1s;
  transition: all 1s;
}

#pdf::after {
  content: "👉";
  font-size: 16px;
  opacity: 0;
}

#pdf2 {
  color: var(--border-color);
  cursor: pointer;
  line-height: 1;
  -webkit-transition: all 1s;
  -o-transition: all 1s;
  transition: all 1s;
  position: absolute;
}

#pdf2:hover {
  color: var(--day-color);
  -webkit-transform: translateX(25px);
  -ms-transform: translateX(25px);
  transform: translateX(25px);
}

#pdf:hover {
  color: var(--day-color);
}

#pdf2::before {
  content: "👉";
  font-size: 16px;
  opacity: 0;
  -webkit-transform: scale(0);
  -ms-transform: scale(0);
  transform: scale(0);
  -webkit-transition: all 1s;
  -o-transition: all 1s;
  transition: all 1s;
}

#pdf2:hover::before {
  font-size: 16px;
  opacity: 100%;
  -webkit-transform: scale(1);
  -ms-transform: scale(1);
  transform: scale(1);
}

